import { $host } from '.';
// import jwt_decode from 'jwt-decode';

export const createType = async (type) => {
    const {data} = await $host.post('api/type', type)
    return data
}

export const fetchTypes = async () => {
    const {data} = await $host.get('api/type')
    return data
}

export const createClothes = async (clothes) => {
    const {data} = await $host.post('api/clothes', clothes)
    return data
}

export const fetchClothing = async (typeId) => {
    const {data} = await $host.get('api/clothes', {
        params: {
            typeId
        }
    })
    return data
}

export const fetchOneClothes = async (id) => {
    const {data} = await $host.get('api/clothes/' + id)
    return data
}